body {
  margin: 0;
  padding: 0;
  font-family: "arial", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #eeeef0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}

* {
  -webkit-appearance: none;
}

*:focus {
  outline: none;
}

html, body {
  height: 100vh;
}